.home-page{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
}

.home-page > *{
    margin: 0px 0px 0px 0px;
}

.homelogo{
    width: 100%;
}

.home-mid{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: flex-start;
    margin-bottom: 30px;
}

.home-picture-holder{
    width: 70vw;
    min-width: 390px;
    height: 41vw;
    padding: 0vh;
    margin: 0px;
    overflow: hidden;
}

.home-photo{
    width: 100%;
}

.notice-board{
    width: 27vw;
    min-width: 400px;
    height: 41vw;
    max-height: 41vw;
    border: 1px solid rgb(0, 0, 0);
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: flex-start;
}

.notice-board-links-holder{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
}

.notice-board-content{
    width: 100%;
    flex: 1 1 auto;
    background-color: black;
    color: white;
    overflow-y: scroll;
}

.events-calender{
    font: normal normal normal 25px/25px 'Times New Roman', Times, serif;
    background-color: #0096b8;
    width:100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin:20px 0px 20px 0px;
    padding: 0px;
}

.callender-activities-header{
    font: normal normal bold 40px/40px 'Courier New', Courier, monospace;
    padding: 10px;
    margin: 10px;
    color: rgb(229, 255, 0);
}

.events-calender-button-holder{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 20px;
}

.events-calender-button{
    font: normal normal normal 20px/20px 'Times New Roman', Times, serif;
    background-color: rgb(240, 240, 240);
    padding: 10px 30px;
    margin: 20px;
    border-radius: 5px;
    border: transparent;
    min-width: 20vw;
    /*transition: padding 0.2s linear 0s, width 0.2s linear 0s;*/
}

.events-calender-button:hover{
    background-color: rgb(121, 185, 95);
    color: white;
    /*filter: brightness(1.5);
    padding: 20px;
    width: 30vw;
    transition: padding 0.2s linear 0s, width 0.2s linear 0s;*/
}

.editor-note{
    width: 80%;
}

.slogan{
    background-color: rgb(151, 225, 255);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.slogan > *{
    width: 90%;
}

@media screen and (max-width:650px){
    .home-page{
        width: 100%;
    }
}


/* hide scrollbar but allow scrolling */
.notice-board-content {
    -ms-overflow-style: none;
    /* for Internet Explorer, Edge */
    scrollbar-width: none;
    /* for Firefox */
    overflow-y: scroll;
}

.notice-board-content::-webkit-scrollbar {
    display: none;
    /* for Chrome, Safari, and Opera */
}