.header {
    min-height: 300px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    text-align:center;
    margin: 0px;
    padding: 0px;
    background-color: rgb(0, 107, 126);
    /*background-image: linear-gradient(to bottom, rgb(255, 249, 163), rgb(0, 0, 0));  */ 
    background-image: url('../images/header-photo1.jpg');
    background-repeat: no-repeat;
    background-size: cover;
}

.header-p{
    width: 80%;
    padding: 10px 20px;
    border-radius: 20px;
    background-color: rgb(107, 176, 255);
    text-align: center;
    color: black;
    font: italic normal normal 20px/20px '';
}

.header-slogan{
    padding: 0px;
    margin: 0px;
    font: normal normal bold 35px/35px '';
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: white;
}
