.flat-nav-link-container{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: end;
    flex-wrap: wrap;
}

.flat-nav-link{
    text-decoration: none;
    display: inline-block;
    padding: 10px;
    border-right: 1px solid rgb(177, 177, 177);
    color: black;
    background-color: white
}

.flat-nav-link:last-child{
    border-right: transparent;
}


.flat-nav-link:hover{
    color: rgb(255, 255, 255);
    background-color: rgb(47, 130, 255);
}