.OrgStructure{
    width: 100%;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.org-structure-diagram{
    width: 90%;
}

.staff-profile-holder{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    background-color: rgb(255, 255, 255);
}

.staff-profile{
    width: 35vw;
    margin: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(180, 180, 180);
}

.staff-profile:hover{
    background-color: rgb(131, 131, 131);
}


.profile-pic{
    width: 70%;
}

.view-full-profile-button{
    color: rgb(0, 0, 167);
    text-decoration: none;
    border: transparent;
    display: block;
    font-size: 15px;
    font-weight: bold;
    font-style: italic;
    padding: 5px 20px;
    cursor: pointer;
    background-color: transparent;
}

.view-full-profile-button:hover{
    color: rgb(0, 255, 0);
    font-weight: bold;
    border-radius: 0px;
}

.hidden-profile{
    display: none;
}


/*//////////////////////////-------------MOBILE VIEW--------------------////////////////////////////////////////*/
@media screen and (max-width:650px) {
    .staff-profile{
        width: 80vw;
    }
}