.my-footer {
  position: relative;
  width: 100%;
  background-color: rgb(0, 62, 73);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-columns-holder {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap; 
  color: #ffffff;
  text-align: left;
}


.summary-column, .links-column, .contact-column{
  max-width: 300px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.links-column{
}

.contact-column{
}

.footer-nav-link{
  display: block;
  text-align:center;
  text-decoration:none;
  padding:5px;
  margin: 0px 0px 3px 0px;
  color: #ffffff;
  font: normal normal normal 15px/15px 'Times New Roman', Times, serif;
}

.footer-nav-link:hover{
  text-decoration: underline;
  background-color: rgb(3, 126, 148);
}


.social-media{
  width: 80%;
  padding: 5px;
  background-color: rgb(0, 62, 73);
  filter: brightness(1.5);
}

.social-media *{
  height: 25px;
  width: auto;
  margin: 0px 10px;
}

.copyright{
  width: 60vw;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  color: white;
  font: normal normal normal 15px/15px 'Times New Roman', Times, serif;
}

.copyright span{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  color: white;
  font: normal normal normal 15px/15px 'Times New Roman', Times, serif;
}

.copyright ul{
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 0px;
  margin: 0px;
}

.copyright ul li{
  display: block;
}

.copyright-link{
  display: block;
  color: white;
  text-decoration: none;
  padding: 5px 10px;
  font: italic normal normal 15px/15px 'Times New Roman', Times, serif;
}

.copyright-link:hover{
  display: block;
  color: white;
  text-decoration: underline;
  padding: 5px 10px;
  font: normal normal bold 15px/15px 'Times New Roman', Times, serif;
}


@media screen and (max-width: 650px) {
  .footer-columns-holder  {
    flex-direction: column;
  }

  .summary-column, .links-column, .contact-column{
    max-width: 100%;
    width: 75%;
    text-align: center;
    border-top: 1px solid rgb(255, 255, 255);
  }
}